import React from 'react'
import '../styles/home.css';
import PORTFOLIOPHOTO from '../images/PORTFOLIO_HOME_PHOTO.png'

const HomePage = () => {

  const scrollIntoAboutSection = () => {
    const about = document.getElementById('AboutPageId');
    about.scrollIntoView();
 }

  return (
    <div className='HomePage' id="HomePageId">
      <div className="homePageOuterMostCon">
      <div className="homePageContentDiv">
        <h1>I'm a Full Stack Web Developer</h1>
        <p>I am full-stack(MERN) developer. I love to work on web applications using technologies like React, Express, Node, MongoDB etc.</p>
        <button className='btn btn-primary' id='AboutPageId' onClick={scrollIntoAboutSection}>About Me</button>
      </div>
      <div className="homePageImageDiv">
        <img src={PORTFOLIOPHOTO} alt="photo" />
      </div>
      </div>
    </div>
  )
}

export default HomePage
