import React, { useState, useRef, useEffect } from 'react'
import emailjs from '@emailjs/browser'
import { message as msg } from 'antd'
// import { Button, Form, Input, message } from 'antd';
// import TextArea from 'antd/es/input/TextArea';

const Contact = () => {
  const [user_name, setUser_name] = useState("")
  const [user_email, setUser_email] = useState("")
  const [message, setMessage] = useState("")
  const [subject, setSubject] = useState("")
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();
    if (user_name && user_email && message) {
      emailjs.sendForm('service_4yk1wgd', 'template_m2za2uv', form.current, 'ELNKh1-vwrVXUFh8M')
        .then((result) => {
          msg.success("Message sent!")
          setUser_name("")
          setUser_email("")
          setMessage("")
          setSubject('')
        }, (error) => {
          msg.error("All fields are mandatory while sending email!")
        })
    };
  }

    return (
      <div className='Contact' id="ContactPageId">
        <div className='commonHeadingDiv'>
          <h2 className="commonHeading">Contact</h2>
          <div className="blank"></div>
        </div>
        <div className="commonParaDiv">
          <p className='commonPara'>Send me a message : </p>
        </div>

        <div className='contactFormDiv'>
          {/* <Form className='contactFormContainer' name="basic" ref={form} onFinish={sendEmail} autoComplete="off">
            <Form.Item name="name" rules={[{ required: true, message: 'Please input your username!', },]}>
              <Input placeholder="Name" />
            </Form.Item>

            <Form.Item name='email' rules={[{ type: 'email', required: true, message: 'Please input your email!' },]}>
              <Input placeholder="Email" />
            </Form.Item>

            <Form.Item name="textArea" rules={[{ required: true, message: 'Please input your answer!', },]}>
              <TextArea placeholder='Message' rows={4} />
            </Form.Item>

            <Form.Item className='registerSubmitBtnCon'>
              <div className="btnCon">
                <Button type="primary" htmlType="submit" className='registerSubmitBtn'>
                  Submit
                </Button>
              </div>
            </Form.Item>
          </Form>  */}
        <form ref={form} onSubmit={sendEmail} >
          <table>
            <tbody>
              <tr className='half'>
                <td>
                  <input type="text" name="user_name" minLength={2} maxLength={30} value={user_name} onChange={(e) => setUser_name(e.target.value)} placeholder='Your Name' required />
                </td>
                <td>
                  <input type="email" name="user_email" value={user_email} onChange={(e) => setUser_email(e.target.value)} placeholder='Your Email' required />
                </td>
              </tr>
              <tr>
                <td>
                  <input type="text" placeholder='Email subject' value={subject} onChange={(e) => setSubject(e.target.value)} required />
                </td>
              </tr>
              <tr>
                <td colSpan={2}>
                  <textarea name="message" value={message} onChange={(e) => setMessage(e.target.value)} placeholder='Your Message' minLength={5} maxLength={150} required />
                </td>
              </tr>
              <tr>
                <td id='ltd' colSpan={2}>
                  <button className='contact_btn' id='contact_btn' type="submit" value={'Send message!'} >Send message!</button>
                </td>
              </tr>
            </tbody>
          </table>
        </form>
        </div>
      </div >
    )
  }

export default Contact
