import React, { useEffect } from 'react';
import { Button, Form, Input, message } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios'
import '../../styles/authStyles/register.css';
import { Link, useNavigate } from 'react-router-dom';
import AdminMenu from './AdminMenu';
import { setSkill } from '../../redux/features/SkillSlice';

const UpdateSkillAdmin = () => {
    const navigate = useNavigate();
    const { skills } = useSelector((state) => state.skills);
    const { skill } = useSelector((state) => state.skill);
    const dispatch = useDispatch();

    const updateSkillFunc = async (id, name) => {
        try {
            const res = await axios.put(`/api/v1/header/updateSkill/${id}`, { name: name });
            if (res.data.success) {
                message.success(res.data.message);
                dispatch(setSkill(null));
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    }

    const onFinish = (values) => {
        try {
            updateSkillFunc(skill?.id, values.name)
            navigate('/admin/create-skill');
        } catch (error) {
            message.error('Something went wrong')
        }
    };

    return (
        <div className="AdminDashboardOutermostCon">
            <div className='AdminDashboard'>
                <div className="AdminDashboardCon">
                    <div className="firstDiv">
                        <AdminMenu />
                    </div>
                    <div className="secondDiv">
                        <div>

                            {skill ? (
                                <Form initialValues={{ ...skill }} className='registerFormContainer' name="basic" onFinish={onFinish} autoComplete="off">
                                    <Form.Item
                                        label="Skill Name" name="name" rules={[{ required: true, message: 'Please input your username!', },]}>
                                        <Input />
                                    </Form.Item>

                                    <Form.Item className='registerSubmitBtnCon'>
                                        <div className="btnCon">
                                            <Button type="primary" htmlType="submit" className='registerSubmitBtn'>
                                                Update
                                            </Button>
                                        </div>
                                    </Form.Item>
                                </Form>
                            ) : (" ")}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default UpdateSkillAdmin
