import React from 'react';
import { Button, Form, Input, message } from 'antd';
import Layout from '../../components/Layout';
import axios from 'axios'
import '../../styles/authStyles/register.css';
import { Link, useNavigate } from 'react-router-dom';

const Register = () => {
  const navigate = useNavigate();

  const onFinish = async (values) => {
    try {
      const res = await axios.post('/api/v1/user/register', values );
      if (res.data?.success) {
        message.success(res.data?.message);
        navigate('/login');
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      message.error('Something went wrong')
    }
  };

  return (
      <div className="registerDiv">
        <Form className='registerFormContainer' name="basic" onFinish={onFinish} autoComplete="off">
          <Form.Item
            label="Username" name="username" rules={[{ required: true, message: 'Please input your username!', },]}>
            <Input />
          </Form.Item>

          <Form.Item name='email' label="Email" rules={[{ type: 'email', required: true, message: 'Please input your email!' },]}>
            <Input />
          </Form.Item>

          <Form.Item label="Password" name="password" rules={[{ required: true, message: 'Please input your password!', },]}>
            <Input.Password />
          </Form.Item>

          <Form.Item label="Contact" name="contact" rules={[{ required: true, message: 'Please input your mobile number!', },]}>
            <Input />
          </Form.Item>

          <Form.Item label="Address" name="address" rules={[{ required: true, message: 'Please input your address!', },]}>
            <Input />
          </Form.Item>

          <Form.Item label="Your Favourite Sport" name="answer" rules={[{ required: true, message: 'Please input your answer!', },]}>
            <Input />
          </Form.Item>

          <Form.Item className='registerSubmitBtnCon'>
            <div className="btnCon">
              <Button type="primary" htmlType="submit" className='registerSubmitBtn'>
                Submit
              </Button>
              <Link to={'/login'} className='registerLink'>Already a user, SignIn</Link>
            </div>
          </Form.Item>
        </Form>
      </div>
  )
}

export default Register
