import { createSlice } from "@reduxjs/toolkit";

export const SkillSlice = createSlice({
    name : "skill",
    initialState : {
        skill : null,
    },
    reducers : {
        setSkill : (state, action) => {
            state.skill = action.payload;
        }
    }
});

export const {setSkill} = SkillSlice.actions;