import React from 'react'
import { Link, NavLink } from 'react-router-dom';
import '../styles/header.css';

const Header = () => {
    const scrollIntoHomeSection = () => {
        const home = document.getElementById('HomePageId');
        home.scrollIntoView();
    };

    const scrollIntoAboutSection = () => {
        const about = document.getElementById('AboutPageId');
        about.scrollIntoView();
    }

    const scrollIntoSkillsSection = () => {
        const skills = document.getElementById('SkillsPageId');
        skills.scrollIntoView();
    }

    const scrollIntoViewWorkSection = () => {
        const work = document.getElementById('WorkPageId');
        work.scrollIntoView();
    }

    const scrollIntoViewContactSection = () => {
        const contact = document.getElementById('ContactPageId');
        contact.scrollIntoView();
    }

    return (
        <>
            <nav className="navbar navbar-expand-lg bg-body-tertiary headerOuterMostCon">
                <div className="container-fluid headerSecondOuterMostCon">
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse headerDiv" id="navbarTogglerDemo01">
                        <div className="logo">
                            <NavLink className="navbar-brand logo_SD" onClick={scrollIntoHomeSection} >SD</NavLink>
                        </div>
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <Link onClick={scrollIntoHomeSection} className="nav-link" aria-current="page">Home</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" onClick={scrollIntoAboutSection}>About</Link>
                            </li>
                            <li className="nav-item">
                                <Link onClick={scrollIntoSkillsSection} className="nav-link">Skills</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" onClick={scrollIntoViewWorkSection}>Work</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" onClick={scrollIntoViewContactSection}>Contact</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </>
    )
}

export default Header
