import { createSlice } from '@reduxjs/toolkit';

export const WorksSlice = createSlice({
    name: "works",
    initialState: {
        works: [],
    },
    reducers: {
        setWorks: (state, action) => {
            state.works = action.payload
        }
    }
});

export const { setWorks } = WorksSlice.actions;