import {createSlice} from '@reduxjs/toolkit';

export const SkillsSlice = createSlice({
    name : 'skills',
    initialState : {
        skills : [],
    },
    reducers : {
        setSkills : (state, action) => {
            state.skills = action.payload;
        }
    }
});

export const {setSkills} = SkillsSlice.actions ;