import React, { useEffect } from 'react'
import axios from 'axios';
import { message } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import '../styles/work.css';
import { setWorks } from '../redux/features/WorksSlice';

const Work = () => {
  const { works } = useSelector((state) => state.works);
  const dispatch = useDispatch();

  //getAllWorksFunc
  const getAllWorksFunc = async () => {
    try {
      const res = await axios.get('/api/v1/work/get-all-works');
      if (res.data?.success) {
        // message.success(res.data.message)
        dispatch(setWorks(res.data.works))
      }
    } catch (error) {
      message.error('Something went wrong')
    }
  }

  useEffect(() => {
    getAllWorksFunc();
  }, []);

  //checkoutFunc
  const checkoutFunc = (link) => {
    window.open(link, "_blank");
  }

  return (
    <div className='Work About' id='WorkPageId'>
      <div className='commonHeadingDiv'>
        <h2 className="commonHeading abtHeading">Work</h2>
        <div className="blank"></div>
      </div>
      <div className="commonParaDiv">
        <p className='commonPara'>Check out some of my works : </p>
      </div>

      <div className="workCon">
        <div className='WorkOuterMostContainer'>
          {works.map((work) => {
            return <div className="workSingleContainer" key={work._id}>
              <div className="workImageCon" style={{backgroundImage : `url(${work.photo})`}}>
                
              </div>
              <div className='workNameCon'>
                <h4>{work.name}</h4>
              </div>
              <div className='workDescCon workContentContainer'>
                <h6 style={{color : "#45B5F8"}}>Description : </h6>
                <p>{work.description}</p>
                <button className='btn btn-primary' onClick={() => { checkoutFunc(work.link) }}>Checkout</button>
              </div>
            </div>
          })}

        </div>
      </div>
    </div>
  )
}

export default Work
