import React, { useEffect, useState } from 'react'
import TagCloud from 'TagCloud'
import { message } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios';
import { setSkills } from '../redux/features/SkillsSlice';

const About = () => {
  const [texts, setTexts] = useState([]);
  const { skills } = useSelector((state) => state.skills);
  const dispatch = useDispatch();

  const tagFunc = (t) => {
    document.getElementById('skill_sphere').innerHTML = ''
    const container = '.skill_sphere';
    const options = {
      radius: 200,
      direction: 135,
      keep: true,
      maxSpeed: 'fast',
      initSpeed: 'fast'
    };

    TagCloud(container, t, options);
  }

  useEffect(() => {
    if (skills.length > 0) {
      let allSkillsNameArr = [];
      skills.map((skill) => {
        return allSkillsNameArr.push(skill.name)
      })
      setTexts([...allSkillsNameArr])
    }
    if (texts.length > 0) {
      tagFunc(texts)
    }
  }, [texts.length, skills.length]);

  const getAllSkills = async () => {
    try {
      const res = await axios.get('/api/v1/header/getSkills');
      if (res.data?.success) {
        dispatch(setSkills(res.data?.skills));
        // message.success(res.data.message);
      }
    } catch (error) {
      message.error('Something went wrong')
    }
  }

  useEffect(() => {
    getAllSkills();
  }, [skills.length]);

  return (
    <div className='About AboutCon' id="AboutPageId">
        <div className='commonHeadingDiv'>
          <h2 className="commonHeading abtHeading">About</h2>
          <div className="blank"></div>
        </div>

        <div className="aboutPageOuterMostCon">
        <div className="skill_sphere SkillCircleCon" id='skill_sphere'>
        </div>
        <div className="homePageContentDiv aboutPageContentDiv">
          <h1>Hi. I'm Sonam</h1>
          <p className='firstPara' >Welcome to my portfolio website!</p> <p className='secondPara'>My name is Sonam Dangi, and I am a full-stack(MERN) developer. I specialize in creating custom websites and web applications that are responsive, user-friendly, and optimized for performance. <br />
            I have expertise in a wide range of web development technologies, including HTML, CSS, JavaScript, and various front-end frameworks. I also have experience working with back-end technologies such as Node, express, mongodb</p>
          {/* <button className='btn btn-primary'>About Me</button> */}
        </div>
      </div>
    </div>

    // <div className='About'>
    //   <div className='commonHeadingDiv'>
    //     <h2 className="commonHeading">About</h2>
    //     <div className="blank"></div>
    //   </div>
    //   <div className="paraDiv">
    //     <p></p>
    //   </div>
    // </div>
  )
}

export default About
