import React, { useEffect, useState } from 'react';
import { Button, Form, Input, message } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios'
import '../../styles/authStyles/register.css';
import { useNavigate } from 'react-router-dom';
import { setWorks } from '../../redux/features/WorksSlice';
import AdminMenu from './AdminMenu';
// import { Link } from 'react-router-dom';
import { AiFillDelete, AiFillEdit } from 'react-icons/ai';
import { setWork } from '../../redux/features/WorkSlice';
import TextArea from 'antd/es/input/TextArea';

const CreateWorkAdmin = () => {
    const navigate = useNavigate();
    const { works } = useSelector((state) => state.works);
    const { work } = useSelector((state) => state.work);
    const dispatch = useDispatch();
    const [isImageSelected, setIsImageSelected] = useState(false);
    const [photo, setPhoto] = useState(null);
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [link, setLink] = useState("");
    const [isEdit, setIsEdit] = useState(false);
    const [editId, setEditId] = useState("");

    //onFinish (create-work)
    const onFinish = async (values) => {
        try {
            if (isEdit == false) {
                if (typeof photo == "string") {
                    const res = await axios.post('/api/v1/work/create-work', { name, description, link, photo });
                    if (res.data?.success) {
                        // message.success(res.data.message);
                        // navigate('/admin/create-work')
                        getAllWorks();
                        window.location.reload();
                    } else {
                        message.error(res.data.message);
                    }
                } else {
                    message.error('Please upload photo!!!')
                }
            } else {
                if (typeof photo == "string") {
                    const res = await axios.put(`/api/v1/work/update-work/${editId}`, { name, description, link, photo });
                    if (res.data?.success) {
                        // message.success(res.data.message);
                        // navigate('/admin/create-work')
                        getAllWorks();
                        setEditId("");
                        setIsEdit(false);
                        window.location.reload();
                    } else {
                        message.error(res.data.message);
                    }
                } else {
                    message.error('Please upload photo!!!')
                }
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    };


    const getAllWorks = async () => {
        try {
            const res = await axios.get('/api/v1/work/get-all-works');
            if (res.data?.success) {
                dispatch(setWorks(res.data.works));
                // message.success(res.data.message);
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    };

    useEffect(() => {
        getAllWorks();
    }, [works.length]);

    //checkoutFunc
    const checkoutFunc = (link) => {
        window.open(link, "_blank");
    }

    //delete-work
    const deleteWorkFunc = async (id) => {
        try {
            const res = await axios.delete(`/api/v1/work/delete-work/${id}`);
            if (res.data?.success) {
                message.success(res.data.message);
                getAllWorks();
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    }

    //get-single-work
    const editWorkFunc = async (id) => {
        try {
            const res = await axios.get(`/api/v1/work/get-single-work/${id}`);
            if (res.data.success) {
                message.success(res.data.message);
                setName(res.data.work.name)
                setDescription(res.data.work.description)
                setLink(res.data.work.link)
                setPhoto(res.data.work.photo)
                setIsEdit(true)
                setEditId(id)
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    }

    //delete-all-work
    const deleteAllWorksFunc = async () => {
        try {
            const res = await axios.delete('/api/v1/work/delete-all-work');
            if (res.data?.success) {
                message.success(res.data.message);
                getAllWorks();
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    }

    // upload image to cloudinary
    const submitImage = () => {
        const data = new FormData()
        data.append('file', photo)
        data.append('upload_preset', 'ecom05')
        data.append('cloud_name', 'dib7eiw3v')
        fetch('https://api.cloudinary.com/v1_1/dib7eiw3v/image/upload', {
            method: 'post',
            body: data
        })
            .then((res) => res.json())
            .then((data) => {
                setPhoto(data.secure_url)
            }).catch((err) => {
                // console.log(err)
            })
    }


    return (
        <div className="AdminDashboardOutermostCon">
            <div className='AdminDashboard'>
                <div className="AdminDashboardCon">
                    <div className="firstDiv">
                        <AdminMenu />
                    </div>
                    <div className="mainDiv">
                        <h2 className='boldifiy'>My Works</h2>
                        <div>
                            <Form className='registerFormContainer' name="basic" onFinish={onFinish} autoComplete="off">
                                <input type='text'
                                    placeholder="Work Name" name="name" required value={name} onChange={(e) => setName(e.target.value)} />


                                <input type='text'
                                    placeholder="Work Description" name="description" required value={description} onChange={(e) => setDescription(e.target.value)} />

                                <input type='text'
                                    placeholder="Work Link" name="link" required value={link} onChange={(e) => setLink(e.target.value)} />


                                <div className="photoContainer">
                                    {isImageSelected === false ? <label className='btn text-dark mb-3 btn-outline-secondary col-md-12'>
                                        Choose Photo
                                        <input accept='image/*' type="file" name="photo" hidden onChange={(e) => { setPhoto(e.target.files[0]); setIsImageSelected(true) }} />
                                    </label> : <label className='btn btn-outline-secondary col-md-12 mb-3 text-dark'>
                                        Upload Photo
                                        <input type="button" onClick={() => { submitImage(); setIsImageSelected(false) }} hidden />
                                    </label>}
                                </div>

                                <div className="photoImageContainer">
                                    {photo && <div>
                                        <div className='photoC' style={{backgroundImage : `url(${typeof photo == "string" ? photo : URL.createObjectURL(photo)})`}}></div>
                                    </div>}
                                </div>

                                <Form.Item className='registerSubmitBtnCon'>
                                    <div className="btnCon">
                                        <Button type="primary" htmlType="submit" className='registerSubmitBtn'>
                                            Submit
                                        </Button>
                                    </div>
                                </Form.Item>
                            </Form>
                        </div>
                        <h3 className='updateSkillHeading'>Update Work</h3>
                        <div className="allSkillsDiv">
                            {works?.length > 0 && works.map((work) => {
                                return <div key={work._id} className="singleWorkDivContainer">
                                    <div className="imgAndTitleContainer">
                                        <div className="photoC" style={{ backgroundImage: `url(${work.photo})` }}></div>
                                        <div className="workTitleContainer">
                                            <div className="workTitle">{work.name}</div>
                                        </div>
                                    </div>
                                    <div className="workContentContainer">
                                        <h3>Description</h3>
                                        <div className="workDesContainer">{work.description}</div>
                                        <div className="checkoutBtnAndIconsContainer">
                                            <button className='btn btn-primary' onClick={() => { checkoutFunc(work.link) }}>Checkout</button>
                                            <div className="workIcons">
                                                <button className='btn btn-primary' onClick={() => { deleteWorkFunc(work._id) }}><AiFillDelete className='deleteIcon' /></button>
                                                <button className='btn btn-primary' onClick={() => { editWorkFunc(work._id) }}><AiFillEdit className='deleteIcon' /></button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            })}
                        </div>
                        <button className='btn btn-danger mt-3 del-btn' onClick={deleteAllWorksFunc}>Delete All Works</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CreateWorkAdmin
