import './App.css';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';
// import Layout from './components/Layout';
import Register from './pages/auth/Register';
import Home from './pages/Home';
import Login from './pages/auth/Login';
// import PublicRoutes from './components/PublicAndProtected/PublicRoutes';
import AdminDashboard from './pages/admin/AdminDashboard';
import CreateSkillAdmin from './pages/admin/CreateSkillAdmin';
import UpdateSkillAdmin from './pages/admin/UpdateSkillAdmin';
import ProtectedRoute from './components/PublicAndProtected/ProtectedRoute';
import CreateWorkAdmin from './pages/admin/CreateWorkAdmin';

function App() {
  return (
    <div className="App">
      <Router>
        <Routes>
          {/* ************auth routes **********/}
          <Route exact path='/register' element={<Register />} />
          <Route exact path='/login' element={<Login />} />

          {/* ************admin routes **********/}
          <Route exact path='/admin-dashboard' element={<ProtectedRoute><AdminDashboard /></ProtectedRoute>} />
          <Route exact path='/admin/create-skill' element={<ProtectedRoute><CreateSkillAdmin /></ProtectedRoute>} />
          <Route exact path='/admin/update-skill' element={<ProtectedRoute><UpdateSkillAdmin /></ProtectedRoute>} />
          <Route exact path='/admin/create-work' element={<ProtectedRoute><CreateWorkAdmin /></ProtectedRoute>} />

          {/* ********** Home page******************* */}
          <Route exact path='/' element={<Home />}/>
        </Routes>
      </Router>
    </div>
  );
}

export default App;
