import React, { useEffect, useState } from 'react';
import { Button, Form, Input, message } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios'
import '../../styles/authStyles/register.css';
import { useNavigate } from 'react-router-dom';
import { setSkills } from '../../redux/features/SkillsSlice';
import AdminMenu from './AdminMenu';
// import { Link } from 'react-router-dom';
import { AiFillDelete } from 'react-icons/ai';
import { setSkill } from '../../redux/features/SkillSlice';

const CreateSkillAdmin = () => {
    const navigate = useNavigate();
    const { skills } = useSelector((state) => state.skills);
    const { skill } = useSelector((state) => state.skill);
    const dispatch = useDispatch();


    const onFinish = async (values) => {
        try {
            const res = await axios.post('/api/v1/header/addSkill', {...values});
            if (res.data?.success) {
                message.success(res.data.message);
                // navigate('/admin/create-skill')
                getAllSkills();
            } else {
                message.error(res.data.message);
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    };

    const getAllSkills = async () => {
        try {
            const res = await axios.get('/api/v1/header/getSkills');
            if (res.data?.success) {
                dispatch(setSkills(res.data.skills));
                // message.success(res.data.message);
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    };

    useEffect(() => {
        getAllSkills();
    }, [skills.length]);

    const deleteSkillFunc = async (id) => {
        try {
            const res = await axios.delete(`/api/v1/header/deleteSkill/${id}`);
            if (res.data?.success) {
                message.success(res.data.message);
                getAllSkills();
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    }

    const deleteAllSkillsFunc = async () => {
        try {
            const res = await axios.delete('/api/v1/header/deleteAllSkills');
            if (res.data?.success) {
                message.success(res.data.message);
                getAllSkills();
            }
        } catch (error) {
            message.error('Something went wrong')
        }
    }

    return (
        <div className="AdminDashboardOutermostCon">
            <div className='AdminDashboard'>
                <div className="AdminDashboardCon">
                    <div className="firstDiv">
                        <AdminMenu />
                    </div>
                    <div className="mainDiv">
                        <h2 className='boldifiy'>My Skills</h2>
                        <div>
                            <Form className='registerFormContainer' name="basic" onFinish={onFinish} autoComplete="off">
                                <Form.Item
                                    label="Skill Name" name="name" rules={[{ required: true, message: 'Please input your username!', },]}>
                                    <Input />
                                </Form.Item>

                                <Form.Item className='registerSubmitBtnCon'>
                                    <div className="btnCon">
                                        <Button type="primary" htmlType="submit" className='registerSubmitBtn'>
                                            Submit
                                        </Button>
                                    </div>
                                </Form.Item>
                            </Form>
                        </div>
                        <h3 className='updateSkillHeading'>Update and Delete Skill</h3>
                        <div className="allSkillsDiv">
                            {skills?.length > 0 && skills.map((skill) => {
                                return <div key={skill._id} className="singleSkillDiv">
                                    <p onClick={() => { dispatch(setSkill({ name: skill.name, id: skill._id })); navigate('/admin/update-skill') }
                                    }>{skill.name}</p>
                                    <AiFillDelete className='deleteIcon' onClick={() => { deleteSkillFunc(skill._id) }} />
                                </div>
                            })}
                        </div>
                        <button className='btn btn-danger mt-3' onClick={deleteAllSkillsFunc}>Delete All Skills</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CreateSkillAdmin
