import React, { useEffect } from 'react'
import { message } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios';
import { setSkills } from '../redux/features/SkillsSlice';

const Skills = () => {
  const { skills } = useSelector((state) => state.skills);
  const dispatch = useDispatch();

  const getAllSkills = async () => {
    try {
      const res = await axios.get('http://localhost:8080/api/v1/header/getSkills');
      if (res.data?.success) {
        dispatch(setSkills(res.data?.skills));
        // message.success(res.data.message);
      }
    } catch (error) {
      message.error('Something went wrong')
    }
  }

  useEffect(() => {
    getAllSkills();
  }, [skills.length]);

  return (
    <div className='About' id="SkillsPageId">
      <div className='commonHeadingDiv'>
        <h2 className="commonHeading">Skills</h2>
        <div className="blank"></div>
      </div>
      <div className="commonParaDiv">
        <p className='commonPara'>I enjoy diving into and learning new things. Here's a list of technologies I've worked with : </p>
      </div>
      <div className="skillsContainer"  >
        <div className="skillsDiv">
          {skills.length > 0 && skills?.map((skill) => {
            return <div key={skill._id} className="singleSkillDiv">
              <p>{skill.name}</p>
            </div>
          })}
        </div>
      </div>
    </div>
  )
}

export default Skills
