import React from 'react'
import { NavLink, useNavigate } from 'react-router-dom'

const AdminMenu = () => {
    const navigate = useNavigate();

    const logoutFunc = () => {
        localStorage.clear();
    }

return (
    <div>
      <div className='admin_menu'>
      <h2>Admin Panel</h2>
            <nav className="nav flex-column">
                <NavLink className="nav-link active" to={'/admin/create-skill'} >Create Skill</NavLink>
                <NavLink className="nav-link" to={'/admin/create-work'}>Create Work</NavLink>
                <NavLink className="nav-link" onClick={logoutFunc} to={'/login'}>Logout</NavLink>
            </nav>
        </div>
    </div>
  )
}

export default AdminMenu
